import * as React from 'react'
import {HashRouter} from 'react-router-dom'
import {Global, css} from '@emotion/core'
import {CssBaseline} from '@material-ui/core'

import ErrorBoundary from './ErrorBoundary'
import AppRouter from './AppRouter'

const globalCss = css({
  '*, *::after, *::before': {
    boxSizing: 'border-box',
  },
  a: {
    textDecoration: 'none',
    color: 'inherit',
  },
  body: {
    // Prevent actions like double-tap-to-zoom
    touchAction: 'manipulation',
  },
})

// When deploying to GH Pages, the application will not be served from root
const BASE_URL = process.env.NODE_ENV === 'production' ? '/webrtc-experiments' : '/'

/** Renders the entire application. */
export default function App(): JSX.Element {
  return (
    <>
      <CssBaseline />
      <ErrorBoundary>
        {/* HashRouter over BrowserRouter since we need routes to work on GH Pages */}
        <HashRouter>
          <Global styles={globalCss} />
          <AppRouter />
        </HashRouter>
      </ErrorBoundary>
    </>
  )
}
